"use client";

import Link from "next/link";
import { motion } from "framer-motion";
import { UMKM_DATA } from "@/data/umkmData";

/* ============================================
   ANIMATION CONFIGURATION
   ============================================ */

const ANIMATION_CONFIG = {
  ITEM: {
    hidden: { opacity: 0, y: 20 }, // No scale (smooth!)
    visible: (custom = 0) => ({
      opacity: 1,
      y: 0,
      transition: {
        duration: 0.55,
        delay: custom * 0.05,
        ease: "easeOut",
      },
    }),
  },
};

/* ============================================
   CTA BUTTON COMPONENT
   ============================================ */

const DetailButton = ({ slug, name }) => (
  <Link href={`/direktori/${slug}`}>
    <motion.button
      whileHover={{ scale: 1.05, translateY: -2 }}
      whileTap={{ scale: 0.95 }}
      className="mt-3 px-4 py-2.5 bg-primary hover:bg-primary/90 text-white rounded-full text-sm font-semibold transition-all focus:ring-2 focus:ring-primary/40 focus:outline-none"
      aria-label={`Lihat detail ${name}`}
      type="button"
    >
      Lihat Detail →
    </motion.button>
  </Link>
);

/* ============================================
   RESTO CARD COMPONENT
   ============================================ */

const RestoCard = ({ resto, variant = "large", custom = 0 }) => {
  const isLarge = variant === "large";

  return (
    <motion.div
      custom={custom}
      variants={ANIMATION_CONFIG.ITEM}
      initial="hidden"
      whileInView="visible"
      viewport={{ once: false, amount: 0.25 }}
      className={`relative group rounded-3xl overflow-hidden shadow-xl transition-all duration-500 ${
        isLarge
          ? "h-[320px] sm:h-[380px] md:h-[420px]"
          : variant === "medium"
            ? "h-[200px] sm:h-[240px] md:h-[280px]"
            : "h-[140px] sm:h-[160px] md:h-[180px]"
      }`}
      whileHover={{
        boxShadow: "0 0 30px rgba(229, 118, 33, 0.25)",
      }}
    >
      {/* Image */}
      <motion.img
        src={resto.img}
        alt={resto.name}
        loading="lazy"
        className="absolute inset-0 w-full h-full object-cover object-center transition-transform duration-700 group-hover:scale-105"
      />

      {/* Overlay Gradient */}
      <div className="absolute inset-0 bg-gradient-to-t from-black/85 via-black/40 to-transparent" />

      {/* Content */}
      <div className={`absolute bottom-0 left-0 right-0 p-6 text-white drop-shadow-md ${isLarge ? "sm:p-8" : ""}`}>
        <h3 className={`font-semibold mb-1.5 line-clamp-2 ${isLarge ? "text-2xl md:text-3xl" : variant === "medium" ? "text-lg md:text-xl" : "text-base md:text-lg"}`}>
          {resto.name}
        </h3>

        <p className={`text-white/85 line-clamp-2 mb-3 ${isLarge ? "text-sm md:text-base" : variant === "medium" ? "text-xs md:text-sm" : "text-xs"}`}>
          {resto.short || resto.desc}
        </p>

        {/* Rating Badge */}
        <motion.div
          className="inline-flex items-center gap-2 px-3 py-1.5 rounded-full bg-primary/90 backdrop-blur-sm"
          whileHover={{ scale: 1.08 }}
        >
          <span className="text-yellow-300 text-sm">★</span>
          <span className="text-white text-sm font-semibold">{resto.rating.toFixed(1)}</span>
        </motion.div>

        {/* CTA Button */}
        <DetailButton slug={resto.slug} name={resto.name} />
      </div>
    </motion.div>
  );
};

/* ============================================
   MAIN TOPRESO COMPONENT
   ============================================ */

export default function TopResto() {
  // Get top 6 restos sorted by rating
  const topRestos = UMKM_DATA.filter((item) => item.category === "resto")
    .sort((a, b) => b.rating - a.rating)
    .slice(0, 6);

  if (topRestos.length < 6) {
    console.warn("Not enough resto data for TopResto section");
  }

  const mainResto = topRestos[1] || topRestos[0];
  const topResto = topRestos[0];
  const gridRestos = topRestos.slice(2, 6);

  return (
    <section
      id="topResto"
      className="relative py-24 md:py-32 overflow-hidden bg-gradient-to-b from-bg-warm via-bg-soft to-bg-base dark:from-bg-warm dark:via-bg-soft dark:to-bg-base transition-colors duration-500"
    >
      <div className="max-w-7xl mx-auto px-6 md:px-10 grid grid-cols-1 lg:grid-cols-12 gap-10">
        {/* === LEFT: Title + Large Image === */}
        <div className="lg:col-span-7 flex flex-col gap-10">
          {/* Header Section */}
          <motion.div
            custom={0}
            variants={ANIMATION_CONFIG.ITEM}
            initial="hidden"
            whileInView="visible"
            viewport={{ once: false, amount: 0.5 }}
          >
            <p className="text-primary font-semibold mb-2 tracking-wide uppercase text-sm">
              Tempat Andalan
            </p>
            <h2 className="text-4xl md:text-5xl font-bold leading-tight mb-4 text-text-primary dark:text-text-secondary">
              Rekomendasi{" "}
              <motion.span
                className="bg-gradient-to-r from-primary to-secondary bg-clip-text text-transparent"
                animate={{
                  backgroundPosition: ["0% 50%", "100% 50%", "0% 50%"],
                }}
                transition={{ duration: 3, repeat: Infinity, ease: "easeInOut" }}
              >
                Paijo
              </motion.span>
            </h2>
            <p className="text-text-muted dark:text-text-secondary/80 text-base md:text-lg max-w-md leading-relaxed">
              Kumpulan tempat makan terbaik di Gelap Nyawang yang telah teruji kelezatannya. Pilihan yang tepat untuk memuaskan selera kuliner kamu!
            </p>
          </motion.div>

          {/* Large Featured Image */}
          {mainResto && (
            <RestoCard resto={mainResto} variant="large" custom={1} />
          )}
        </div>

        {/* === RIGHT: Top Image + 4 Grid === */}
        <div className="lg:col-span-5 flex flex-col gap-10">
          {/* Top Medium Image */}
          {topResto && (
            <RestoCard resto={topResto} variant="medium" custom={2} />
          )}

          {/* Grid 4 Small Images */}
          <div className="grid grid-cols-2 gap-6">
            {gridRestos.map((resto, i) => (
              <RestoCard
                key={resto.id}
                resto={resto}
                variant="small"
                custom={3 + i}
              />
            ))}
          </div>
        </div>
      </div>

      {/* Subtle Bottom Gradient */}
      <div
        className="absolute bottom-0 left-0 w-full h-[120px] bg-gradient-to-t from-bg-base to-transparent dark:from-bg-base pointer-events-none"
        aria-hidden="true"
      />
    </section>
  );
}